import REFRESH_DATA from './constantas/action-types';
import data from './utils/data';
import { SORT_CONFIG } from './constantas/settings';

const initialState = {
  initialData: data,
  filteredData: data,
  shownData: [],
  selected: [],
  filterConfig: SORT_CONFIG,
  isVirtual: true,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case REFRESH_DATA:
      return { ...state, ...action.payload };
    default:
      return state;
  }
};

export default reducer;
