const toggleStatus = (isSelected, filteredData, refreshFilteredData, refreshLS, config) => {
  let newData = [];
  if (isSelected) {
    filteredData.forEach(el => {
      if (el.status === 'operating') {
        newData.push(el);
      }
    });
  } else {
    newData = filteredData.slice();
  }
  const newConfig = {
    ...config,
    status: { isShowOnlyOperating: isSelected, statusToShow: isSelected ? 'operating' : 'all' },
  };
  refreshLS(newConfig);
  return newData;
};

export default toggleStatus;
