function draw() {
  // grain is basically level of detail. higher = finer.
  const grain = 5;
  let blockout = Math.random();
  const canvas = document.createElement('CANVAS');
  canvas.width = 30;
  canvas.height = 30;
  document.body.append(canvas);
  const w = 30;
  const h = 30;
  const colorArray = [];
  let pokeOut;
  let dataURI;
  if (canvas.getContext) {
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, w, h);
    ctx.fillStyle = 'transparent';
    ctx.fillRect(0, 0, w, h);

    const randomRGB = (min, max) => {
      const minX = Math.ceil(min);
      const maxX = Math.floor(max);
      for (let i = 0; i < 3; i += 1) {
        colorArray.push(Math.floor(Math.random() * (maxX - minX)) + minX);
      }
    };

    pokeOut = () => {
      randomRGB(0, 255);
      let posX = 0;
      let posY = 0;
      let startFillRed = colorArray[0];
      let startFillGreen = colorArray[1];
      let startFillBlue = colorArray[2];
      const colorRange = 5;
      ctx.fillStyle = `rgb(${startFillRed},${startFillGreen},${startFillBlue})`;

      for (let y = 0; y < grain; y += 1) {
        for (let x = 0; x < grain; x += 1) {
          if (blockout < 0.4) {
            ctx.fillRect(posX, posY, w / grain, h / grain);
            ctx.fillRect(w - posX - w / grain, posY, w / grain, h / grain);
            posX += w / grain;
          } else {
            startFillRed -= colorRange;
            startFillGreen += colorRange;
            startFillBlue += colorRange;
            ctx.fillStyle = `rgb(${startFillRed},${startFillGreen},${startFillBlue})`;
            posX += w / grain;
          }
          blockout = Math.random();
        }
        posY += h / grain;
        posX = 0;
      }

      dataURI = canvas.toDataURL();
      canvas.remove();
    };
  }
  pokeOut();
  return dataURI;
}

export default draw;
