import Fakerator from 'fakerator';
import drawIcon from './icon-generator';

const dataGenerator = (bussinessTypes, companyStatus, minInvestSum, maxInvestSum) => {
  const fakerator = Fakerator('en-EN');

  fakerator.seed(Math.floor(Math.random() * 10000000));

  const id = fakerator.random.masked('aaa-AAA_999999:*');
  const icon = drawIcon();
  const company = fakerator.company.name();
  const url = `https://${fakerator.random.string(10)}.com`;
  const foundedYear = fakerator.date.past(10, new Date()).getFullYear();
  const founder = fakerator.names.name();
  const fundingTotal = fakerator.random.number(minInvestSum, maxInvestSum);
  const stillAlive = fakerator.random.boolean();
  const status = stillAlive ? companyStatus[0] : companyStatus[1];
  const geo = fakerator.address.geoLocation();
  const businessTypes = [];

  while (businessTypes.length === 0) {
    for (let j = 0; j < 2; j += 1) {
      const k = fakerator.random.number(0, 5);
      const value = bussinessTypes[k];
      if (value !== undefined && !businessTypes.includes(value)) {
        businessTypes.push(value);
      }
    }
  }
  return {
    id,
    icon,
    company,
    url,
    foundedYear,
    founder,
    fundingTotal,
    status,
    geo,
    businessTypes,
  };
};

export default dataGenerator;
