// Алгоритм работы фильтра:
// 1) изначально filteredData = initialData
// 2) берем filterConfig, config = filterConfig,
// 3) применяем по очереди фильтры соответсвенно config
// 4) обновляем filteredData
// 5) получаем shownData от filteredData
// 6) слушаем изменения config
// 7) записываем изменения config в filterConfig
// 8) при появлении новых значений фильтров, повторям от шага 2 заново
const ConnectedFilters = props => {
  // шаг 1) изначально filteredData = initialData
  const { refreshData, initialData, filteredData, isVirtual } = props;
  const styles = useStyles();

  // шаг 2) берем filterConfig, config = filterConfig,
  const config = JSON.parse(localStorage.getItem(DATAGRID_SORT_CONFIG));
  const [checkedCategories, setCheckedCategories] = useState([]);
  let filteredDataAfterCheckboxes = filteredData.slice();
  const [searchAll, setSearchAll] = useState('');
  const [searchedCompany, setSearchedCompany] = useState('');
  const [searchedFounder, setSearchedFounder] = useState('');
  let status = config.status.statusToShow;
  const [yearsRange, setYearsRange] = React.useState([MIN_YEAR, MAX_YEAR]);
  const [fundRange, setFundRange] = React.useState([MIN_INVEST_SUM, MAX_INVEST_SUM]);
  const checkedCategories1 = [];
  const labels = [...COMPANY_STATUS, 'all'];

  // подготовка значений для checkbox фильтрации
  const categoryMap = new Map();
  BUSINESS_TYPES.forEach((type, i) => {
    categoryMap.set(type, config.businessTypes.categories[i]);
    if (config.businessTypes.categories[i]) {
      checkedCategories1.push(type);
    }
  });

  const handleResetFilter = () => {
    // шаг 7) записываем изменения config
    refreshLS(SORT_CONFIG);
    setCheckedCategories([]);
  };

  const handleCompanySearch = ({ target: { value } }) => {
    const newData = [];
    filteredDataAfterCheckboxes.forEach(el => {
      if (el.company.toLowerCase().includes(value.toLowerCase())) {
        newData.push(el);
      }
    });
    config.company.searchValue = value;
    setSearchedCompany(value);
    // шаг 4) обновляем filteredData
    refreshData({ filteredData: newData });
    filteredDataAfterCheckboxes = newData.slice();

    // шаг 7) записываем изменения config в filterConfig
    refreshLS(config);
  };

  const handleFoundersSearch = ({ target: { value } }) => {
    const newData = [];
    filteredDataAfterCheckboxes.forEach(el => {
      if (el.founder.toLowerCase().includes(value.toLowerCase())) {
        newData.push(el);
      }
    });
    config.founder.searchValue = value;
    setSearchedFounder(value);
    // шаг 4) обновляем filteredData
    refreshData({ filteredData: newData });
    filteredDataAfterCheckboxes = newData.slice();

    // шаг 7) записываем изменения config в filterConfig
    refreshLS(config);
  };

  const handleSearch = ({ target: { value } }) => {
    const newData = [];
    filteredDataAfterCheckboxes.forEach(el => {
      if (
        el.founder.toLowerCase().includes(value.toLowerCase()) ||
        el.company.toLowerCase().includes(value.toLowerCase())
      ) {
        newData.push(el);
      }
    });
    config.searchByAllStr = value;
    setSearchAll(value);
    // шаг 4) обновляем filteredData
    refreshData({ filteredData: newData });
    filteredDataAfterCheckboxes = newData.slice();

    // шаг 7) записываем изменения config в filterConfig
    refreshLS(config);
  };

  const handleCheckboxChange = ({ target }) => {
    const { value, checked } = target;
    categoryMap.set(value, checked);

    const set = new Set(checkedCategories);
    if (checked) {
      set.add(value);
    } else {
      set.delete(value);
    }

    const typeIndex = BUSINESS_TYPES.indexOf(value);
    config.businessTypes.categories[typeIndex] = checked;
    const newArr = [...set];
    setCheckedCategories(newArr);

    // шаг 7) записываем изменения config в filterConfig
    refreshLS(config);
  };

  const handleStatusChange = ({ target: { value } }) => {
    let newData = [];
    if (value === 'all') {
      newData = [...filteredDataAfterCheckboxes];
    } else {
      filteredDataAfterCheckboxes.forEach(el => {
        if (el.status === value) {
          newData.push(el);
        }
      });
    }
    config.status.statusToShow = value;
    config.status.isShowOnlyOperating = value === 'operating';

    // шаг 4) обновляем filteredData
    refreshData({ filteredData: newData });
    status = value;
    filteredDataAfterCheckboxes = newData.slice();

    // шаг 7) записываем изменения config в filterConfig
    refreshLS(config);
    refreshData({ filterConfig: config });
  };

  const yearsRangeFilter = range => {
    const newData = [];
    filteredDataAfterCheckboxes.forEach(el => {
      if (el.foundedYear >= range[0] && el.foundedYear <= range[1]) {
        newData.push(el);
      }
    });
    config.foundedYear.searchRange = range;

    // шаг 4) обновляем filteredData
    refreshData({ filteredData: newData });
    filteredDataAfterCheckboxes = newData.slice();
    // setYearsRange(range);

    // шаг 7) записываем изменения config в filterConfig
    refreshLS(config);
  };

  const fundRangeFilter = range => {
    const newData = [];
    filteredDataAfterCheckboxes.forEach(el => {
      if (el.fundingTotal >= range[0] && el.fundingTotal <= range[1]) {
        newData.push(el);
      }
    });
    config.fundingTotal.searchRange = range;

    // шаг 4) обновляем filteredData
    refreshData({ filteredData: newData });
    filteredDataAfterCheckboxes = newData.slice();
    // setFundRange(range);

    // шаг 7) записываем изменения config в filterConfig
    refreshLS(config);
  };

  // TODO add to checkbox - None ofthis businesses

  // шаг 3) применяем по очереди фильтры соответсвенно config
  const filtering = () => {
    // checkbox with Type of businesses
    let newData = [];
    if (![0, BUSINESS_TYPES.length].includes(checkedCategories.length)) {
      initialData.forEach(el => {
        const el1 = el.businessTypes.map(category => {
          return checkedCategories.includes(category);
        });
        if (el1.some(element => element === true)) {
          newData.push(el);
        }
      });
    } else {
      newData = initialData.slice();
    }
    // шаг 4) обновляем filteredData

    filteredDataAfterCheckboxes = newData.slice();

    // input company
    const searchedCompany1 = config.company.searchValue;
    handleCompanySearch({ target: { value: searchedCompany1 } });

    // input founder
    const searchedFounder1 = config.founder.searchValue;
    handleFoundersSearch({ target: { value: searchedFounder1 } });

    // radio status
    const searchedStatus = config.status.statusToShow;
    handleStatusChange({ target: { value: searchedStatus } });

    // years range filter
    const yearsRange1 = config.foundedYear.searchRange;
    yearsRangeFilter(yearsRange1);

    // fund range filter
    const fundRange1 = config.fundingTotal.searchRange;
    fundRangeFilter(fundRange1);

    // search by all
    const search = config.searchByAllStr;
    handleSearch({ target: { value: search } });

    // sorting
    const { orderBy } = config;
    const order = config[orderBy].sortDirection;
    const sortedData = stableSort(filteredDataAfterCheckboxes, getComparator(order, orderBy));
    refreshData({ filteredData: sortedData });
    filteredDataAfterCheckboxes = sortedData.slice();
    // status toggling
    const isSelected = config.status.isShowOnlyOperating;
    if (isSelected) {
      handleStatusChange({ target: { value: 'operating' } });
    }
  };

  const handleYearsRange = (e, newValue) => {
    yearsRangeFilter(newValue);
    setYearsRange(newValue);
  };
  const handleMinYearChange = ({ target: { value } }) => {
    if (value <= yearsRange[1]) {
      const newRange = [Number(value), yearsRange[1]];
      yearsRangeFilter(newRange);
      setYearsRange(newRange);
    }
  };
  const handleMaxYearChange = ({ target: { value } }) => {
    if (value >= yearsRange[0]) {
      const newRange = [yearsRange[0], Number(value)];
      yearsRangeFilter(newRange);
      setYearsRange(newRange);
    }
  };
  const handleYearBlur = () => {
    if (yearsRange[0] < MIN_YEAR) {
      const newRange = [MIN_YEAR, yearsRange[1]];
      yearsRangeFilter(newRange);
      setYearsRange([MIN_YEAR, yearsRange[1]]);
    } else if (yearsRange[1] > MAX_YEAR) {
      const newRange = [yearsRange[0], MAX_YEAR];
      yearsRangeFilter(newRange);
      setYearsRange([yearsRange[0], MAX_YEAR]);
    }
  };

  const handleFundRange = (e, newValue) => {
    fundRangeFilter(newValue);
    setFundRange(newValue);
  };
  const handleMinFundChange = ({ target: { value } }) => {
    if (value <= fundRange[1]) {
      const newRange = [Number(value), fundRange[1]];
      fundRangeFilter(newRange);
      setFundRange(newRange);
    }
  };
  const handleMaxFundChange = ({ target: { value } }) => {
    if (value >= fundRange[0]) {
      const newRange = [fundRange[0], Number(value)];
      fundRangeFilter(newRange);
      setFundRange(newRange);
    }
  };
  const handleFundBlur = () => {
    if (fundRange[0] < MIN_INVEST_SUM) {
      const newRange = [MIN_INVEST_SUM, fundRange[1]];
      fundRangeFilter(newRange);
      setFundRange([MIN_INVEST_SUM, fundRange[1]]);
    } else if (fundRange[1] > MAX_INVEST_SUM) {
      const newRange = [fundRange[0], MAX_INVEST_SUM];
      fundRangeFilter(newRange);
      setFundRange([fundRange[0], MAX_INVEST_SUM]);
    }
  };

  // шаг 6) слушаем изменения config
  useEffect(() => {
    filtering();
  }, [
    checkedCategories,
    searchedCompany,
    searchedFounder,
    status,
    searchAll,
    isVirtual,
    yearsRange,
    fundRange,
  ]);

  useEffect(() => {
    setCheckedCategories(checkedCategories1);
  }, []);

  return (
    <div className={styles.wrap}>
      <div className={styles.filters}>
        <div className={styles.header}>
          <Typography variant="h3">Filters</Typography>
          <div className={styles.search}>
            <div className={styles.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search everywhere"
              classes={{
                root: styles.inputRoot,
                input: styles.inputHeader,
              }}
              inputProps={{ 'aria-label': 'Search everywhere' }}
              onChange={handleSearch}
              value={config.searchByAllStr || ''}
            />
          </div>
        </div>
        <Divider />

        <div className={styles.filter}>
          <Typography className={styles.title}>Company</Typography>
          <div className={styles.search}>
            <div className={styles.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search by company name"
              classes={{
                root: styles.inputRoot,
                input: styles.inputInput,
              }}
              inputProps={{ 'aria-label': 'Search by company name' }}
              onChange={handleCompanySearch}
              value={config.company.searchValue || ''}
            />
          </div>
        </div>

        <div className={styles.filter}>
          <Typography className={styles.title}>Founder</Typography>
          <div className={styles.search}>
            <div className={styles.searchIcon}>
              <SearchIcon />
            </div>
            <InputBase
              placeholder="Search by company founders"
              classes={{
                root: styles.inputRoot,
                input: styles.inputInput,
              }}
              inputProps={{ 'aria-label': 'Search by company founders' }}
              onChange={handleFoundersSearch}
              value={config.founder.searchValue}
            />
          </div>
        </div>

        <div className={styles.filter}>
          <FormControl component="fieldset" className={styles.formControl}>
            <FormLabel component="legend" className={styles.title}>
              Type of business
            </FormLabel>
            <FormGroup>
              {BUSINESS_TYPES.map(category => {
                return (
                  <FormControlLabel
                    key={category}
                    control={
                      <Checkbox
                        checked={categoryMap.get(category)}
                        onChange={handleCheckboxChange}
                        value={category}
                        color="default"
                        checkedIcon={<span className={clsx(styles.icon, styles.checkedIcon)} />}
                        icon={<span className={styles.icon} />}
                        inputProps={{ 'aria-label': category }}
                      />
                    }
                    label={category}
                  />
                );
              })}
            </FormGroup>
          </FormControl>
        </div>

        <div className={styles.filter}>
          <FormControl component="fieldset">
            <FormLabel component="legend" className={styles.title}>
              Status of company
            </FormLabel>
            <RadioGroup
              aria-label="Status of compan"
              name="Status of compan"
              onChange={handleStatusChange}
              value={status}
            >
              {labels.map(label => (
                <FormControlLabel
                  key={label}
                  control={<StyledRadio />}
                  value={label}
                  label={label}
                />
              ))}
            </RadioGroup>
          </FormControl>
        </div>

        <div className={styles.filter}>
          <Typography className={styles.title}>Year of foundation range</Typography>
          <Slider
            min={MIN_YEAR}
            max={MAX_YEAR}
            step={1}
            value={yearsRange}
            onChange={handleYearsRange}
            valueLabelDisplay="auto"
            aria-labelledby="Year of foundation range"
            className={styles.range}
          />
          <div className={styles.inputs}>
            <Input
              className={styles.input}
              value={yearsRange[0]}
              onChange={handleMinYearChange}
              onBlur={handleYearBlur}
              inputProps={{
                step: 1,
                min: MIN_YEAR,
                max: MAX_YEAR,
                type: 'number',
                'aria-labelledby': 'minimum value in years range of company released',
              }}
            />
            <Input
              className={styles.input}
              value={yearsRange[1]}
              onChange={handleMaxYearChange}
              onBlur={handleYearBlur}
              inputProps={{
                step: 1,
                min: MIN_YEAR,
                max: MAX_YEAR,
                type: 'number',
                'aria-labelledby': 'maximum value in years range of company released',
              }}
            />
          </div>
        </div>
      </div>

      <div className={styles.filter}>
        <Typography className={styles.title}>
          Funding total, USD &#215; 10<sup>3</sup>
        </Typography>
        <Slider
          min={MIN_INVEST_SUM}
          max={MAX_INVEST_SUM}
          step={5}
          value={fundRange}
          onChange={handleFundRange}
          valueLabelDisplay="auto"
          aria-labelledby="Year of foundation range"
          className={styles.range}
        />
        <div className={styles.inputs}>
          <Input
            className={styles.input}
            value={fundRange[0]}
            onChange={handleMinFundChange}
            onBlur={handleFundBlur}
            inputProps={{
              step: 5,
              min: MIN_INVEST_SUM,
              max: MAX_INVEST_SUM,
              type: 'number',
              'aria-labelledby': 'minimum value of company funding',
            }}
          />
          <Input
            className={styles.input}
            value={fundRange[1]}
            onChange={handleMaxFundChange}
            onBlur={handleFundBlur}
            inputProps={{
              step: 5,
              min: MIN_INVEST_SUM,
              max: MAX_INVEST_SUM,
              type: 'number',
              'aria-labelledby': 'maximum value of company funding',
            }}
          />
        </div>
      </div>
      <Button
        variant="outlined"
        color="primary"
        className={styles.defaultSettings}
        onClick={handleResetFilter}
      >
        Reset filters
      </Button>
    </div>
  );
};

ConnectedFilters.defaultProps = {
  initialData: [],
  filteredData: [],
  isVirtual: true,
  refreshData: () => {},
};

ConnectedFilters.propTypes = {
  initialData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      fundingTotal: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      businessTypes: PropTypes.arrayOf(PropTypes.string),
    })
  ),
  filteredData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      fundingTotal: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      businessTypes: PropTypes.arrayOf(PropTypes.string),
    })
  ),
  isVirtual: PropTypes.bool,
  refreshData: PropTypes.func,
};

const mapStateToProps = ({ initialData, filteredData, isVirtual }) => ({
  initialData,
  filteredData,
  isVirtual,
});

const mapDispatchToProps = dispatch => {
  return {
    refreshData: data => dispatch(refreshData(data)),
  };
};

// TODO стилизовать фильтр как навбар при разрешении менее 600, чтоб выдвигался сбоку из гамбургера элемент Drawer из  material
const FiltersWithoutRouter = connect(mapStateToProps, mapDispatchToProps)(ConnectedFilters);
const Filters = withRouter(FiltersWithoutRouter); // TODO query string for
export default Filters;
