import React from 'react';
// import queryString from 'query-string';
import { CssBaseline, ThemeProvider } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import theme from '../themes/themeLight';
import DataGrid from './table/datagrid';
import Filters from './filters/filters';
import { SORT_CONFIG, DATAGRID_SORT_CONFIG } from '../constantas/settings';

const useStyles = makeStyles({
  container: {
    display: 'flex',
    flexWrap: 'nowrap',
    justifyContent: 'center',
    padding: 20,
  },
});

const App = () => {
  const styles = useStyles();
  let sortConfig;
  const sortConfigJSON = localStorage.getItem(DATAGRID_SORT_CONFIG);
  try {
    sortConfig = { ...(sortConfigJSON === null ? SORT_CONFIG : JSON.parse(sortConfigJSON)) };
  } catch (e) {
    localStorage.removeItem(DATAGRID_SORT_CONFIG);
    sortConfig = { ...SORT_CONFIG };
  }
  localStorage.removeItem(DATAGRID_SORT_CONFIG);
  localStorage.setItem(DATAGRID_SORT_CONFIG, JSON.stringify(sortConfig));

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <div className={styles.container}>
        <Filters />
        <DataGrid />
      </div>
    </ThemeProvider>
  );
};

export default App;

