/* eslint-disable no-shadow */
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

import refreshData from '../../actions';
import getData from '../../utils/get-data';
import { DATAGRID_SORT_CONFIG } from '../../constantas/settings';
import { getComparator, stableSort } from '../../utils/sort-functions';
import TableHeader from './table-head';
import RowTemplate from './row-template';
import refreshLS from '../../utils/save-in-LS';

const useStyles = makeStyles({
  table: {
    width: '100%',
    minWidth: 300,
    height: 900,
    overflow: 'scroll',
  },
  empty: {
    width: '100%',
    height: 60,
    backgroundColor: 'transparent',
    border: 'none',
  },
});

const ConnectedSimpleTable = props => {
  const styles = useStyles();

  const config = JSON.parse(localStorage.getItem(DATAGRID_SORT_CONFIG));

  const { selected, filteredData, refreshData } = props;
  const [orderBy, setOrderBy] = useState(config.orderBy);
  const [order, setOrder] = useState(config[orderBy].sortDirection);
  const [scrollLeftState, setScrollLeftState] = useState(0);

  const runScroller = ({ target }) => {
    const { scrollLeft } = target;
    setScrollLeftState(scrollLeft);
  };

  useEffect(() => {
    const data = getData(0, 25, 0, filteredData);
    refreshData({ shownData: data });
  }, [filteredData]);

  const handleRequestSort = property => {
    const isAsc = orderBy === property && order === 'asc';

    const newOrder = isAsc ? 'desc' : 'asc';
    setOrder(newOrder);
    setOrderBy(property);
    const sortedData = stableSort(filteredData, getComparator(order, orderBy));
    refreshLS(config);
    refreshData({ shownData: sortedData });
  };

  return (
    <div className={styles.table} onScroll={runScroller}>
      <TableHeader
        numSelected={selected.length}
        order={order}
        onRequestSort={handleRequestSort}
        rowCount={filteredData.length}
        scrollLeft={scrollLeftState}
      />
      <div className={styles.empty} />
      {filteredData.map(item => (
        <RowTemplate item={item} key={item.id} scrollLeft={scrollLeftState} />
      ))}
    </div>
  );
};

ConnectedSimpleTable.defaultProps = {
  selected: [],
  filteredData: {},
  refreshData: () => {},
};

ConnectedSimpleTable.propTypes = {
  selected: PropTypes.arrayOf(PropTypes.string),
  filteredData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      fundingTotal: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      businessTypes: PropTypes.arrayOf(PropTypes.string),
    })
  ),
  refreshData: PropTypes.func,
};

const mapStateToProps = ({ initialData, filteredData, selected }) => ({
  initialData,
  filteredData,
  selected,
});

const mapDispatchToProps = dispatch => {
  return {
    refreshData: rows => dispatch(refreshData(rows)),
  };
};

const SimpleTable = connect(mapStateToProps, mapDispatchToProps)(ConnectedSimpleTable);

export default SimpleTable;

