/* eslint-disable no-shadow */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core';

import refreshData from '../../actions';
import TableToolbar from './table-toolbar';
import Switcher from '../switcher';
import VirtualTable from './table-virtual';
import SimpleTable from './table-simple';

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    width: '100%',
    height: '100%',
    maxWidth: 1200,
    minWidth: 400,
    boxShadow: theme.shadows[3],
  },
}));
const ConnectedDataGrid = ({ isVirtual, selected, refreshData, initialData }) => {
  const classes = useStyles();
  const [isVirtualChecked, setIsVirtualChecked] = React.useState(isVirtual);

  const toggleChecked = () => {
    setIsVirtualChecked(!isVirtualChecked);
    refreshData({ isVirtual: !isVirtual });
  };

  const handleDelete = () => {
    const newArr = initialData.filter(el => !selected.includes(el.id));
    refreshData({ initialData: newArr, selected: [], filteredData: newArr });
  };

  return (
    <div className={classes.root}>
      <Switcher checked={isVirtualChecked} toggleChecked={toggleChecked} />
      <TableToolbar handleDelete={handleDelete} />
      {isVirtualChecked ? <VirtualTable /> : <SimpleTable />}
    </div>
  );
};

ConnectedDataGrid.defaultProps = {
  initialData: [],
  isVirtual: true,
  selected: [],
  refreshData: () => {},
};

ConnectedDataGrid.propTypes = {
  initialData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      fundingTotal: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      businessTypes: PropTypes.arrayOf(PropTypes.string),
    })
  ),
  isVirtual: PropTypes.bool,
  selected: PropTypes.arrayOf(PropTypes.string),
  refreshData: PropTypes.func,
};

const mapStateToProps = ({ isVirtual, initialData, selected }) => ({
  isVirtual,
  initialData,
  selected,
});

const mapDispatchToProps = dispatch => {
  return {
    refreshData: data => dispatch(refreshData(data)),
  };
};

const DataGrid = connect(mapStateToProps, mapDispatchToProps)(ConnectedDataGrid);

export default DataGrid;
