import dataGenerator from './data-generator';
import { ROW_NUMBER } from '../constantas/settings';
import {
  BUSINESS_TYPES,
  COMPANY_STATUS,
  MIN_INVEST_SUM,
  MAX_INVEST_SUM,
} from '../constantas/data-generator-for';

const data = (n, generator, bussinessTypes, companyStatus, minInvestSum, maxInvestSum) => {
  const arr = [];
  for (let i = 0; i < n; i += 1) {
    arr.push({
      ...generator(bussinessTypes, companyStatus, minInvestSum, maxInvestSum),
      ...{ index: i },
    });
  }
  return arr;
};

export default data(
  ROW_NUMBER,
  dataGenerator,
  BUSINESS_TYPES,
  COMPANY_STATUS,
  MIN_INVEST_SUM,
  MAX_INVEST_SUM
);
