/* eslint-disable no-shadow */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import {
  IconButton,
  Tooltip,
  Popover,
  FormControl,
  FormGroup,
  FormControlLabel,
  Checkbox,
} from '@material-ui/core';
import MoreVertIcon from '@material-ui/icons/MoreVert';

import refreshData from '../actions';
import refreshLS from '../utils/save-in-LS';

const useStyles = makeStyles(theme => ({
  columnToggler: {
    padding: 5,
  },
  icon: {
    borderRadius: 3,
    width: 16,
    height: 16,
    boxShadow: `inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)`,
    backgroundColor: theme.palette.background.default,
    backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
    '$root.Mui-focusVisible &': {
      outline: '2px auto rgba(19,124,189,.6)',
      outlineOffset: 2,
    },
    'input:hover ~ &': {
      backgroundColor: theme.palette.secondary.light,
    },
    'input:disabled ~ &': {
      boxShadow: 'none',
      background: 'rgba(206,217,224,.5)',
    },
  },
  checkedIcon: {
    backgroundColor: theme.palette.primary.main,
    backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))',
    boxShadow: `inset 0 0 0 1px rgba(16,22,26,.1), inset 0 -1px 0 rgba(16,22,26,.1)`,
    '&:before': {
      display: 'block',
      width: 16,
      height: 16,
      backgroundImage:
        "url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath" +
        " fill-rule='evenodd' clip-rule='evenodd' d='M12 5c-.28 0-.53.11-.71.29L7 9.59l-2.29-2.3a1.003 " +
        "1.003 0 00-1.42 1.42l3 3c.18.18.43.29.71.29s.53-.11.71-.29l5-5A1.003 1.003 0 0012 5z' fill='%23fff'/%3E%3C/svg%3E\")",
      content: '""',
    },
    'input:hover ~ &': {
      backgroundColor: theme.palette.primary.light,
    },
  },
  popover: {
    '& .MuiPopover-paper': {
      padding: 10,
    },
  },
}));

const ConnectedColumnToggle = props => {
  const styles = useStyles();
  const { filterConfig, refreshData } = props;

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const btnId = open ? 'Column viewing toggle' : undefined;

  const handleChange = idCurrent => () => {
    const newCells = filterConfig.columnConfig.map(item => {
      const { id, isVisible } = item;
      return id === idCurrent ? { ...item, isVisible: !isVisible } : item;
    });
    const newConfig = { ...filterConfig, columnConfig: newCells };
    refreshLS(newConfig);
    refreshData({ filterConfig: newConfig });
  };

  return (
    <>
      <Tooltip title="Column viewing toggle">
        <IconButton
          aria-label="Column viewing toggle"
          className={styles.columnToggler}
          onClick={handleClick}
          aria-describedby={btnId}
        >
          <MoreVertIcon />
        </IconButton>
      </Tooltip>
      <Popover
        disableScrollLock
        id={btnId}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        className={styles.popover}
      >
        <FormControl component="fieldset" className={styles.formControl}>
          <FormGroup>
            {filterConfig.columnConfig.map(({ id, label, isVisible }) => {
              return label !== 'Company' ? (
                <FormControlLabel
                  key={label}
                  control={
                    <Checkbox
                      checked={isVisible}
                      onChange={handleChange(id)}
                      value={label}
                      color="default"
                      checkedIcon={<span className={clsx(styles.icon, styles.checkedIcon)} />}
                      icon={<span className={styles.icon} />}
                      inputProps={{ 'aria-label': label }}
                    />
                  }
                  label={label}
                />
              ) : null;
            })}
          </FormGroup>
        </FormControl>
      </Popover>
    </>
  );
};
ConnectedColumnToggle.defaultProps = {
  filterConfig: {},
  refreshData: () => {},
};

ConnectedColumnToggle.propTypes = {
  filterConfig: PropTypes.shape({
    orderBy: PropTypes.string,
    index: PropTypes.shape({
      sortDirection: PropTypes.string,
    }),
    company: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchValue: PropTypes.string,
    }),
    foundedYear: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchRange: PropTypes.arrayOf(PropTypes.number),
    }),
    founder: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchValue: PropTypes.string,
    }),
    fundingTotal: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchRange: PropTypes.arrayOf(PropTypes.number),
    }),
    businessTypes: PropTypes.shape({
      sortDirection: PropTypes.string,
      categories: PropTypes.arrayOf(PropTypes.bool),
    }),
    status: PropTypes.shape({
      isShowOnlyOperating: PropTypes.bool,
      statusToShow: PropTypes.string,
    }),
    searchByAllStr: PropTypes.string,
    columnConfig: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string,
        label: PropTypes.string,
        isVisible: PropTypes.bool,
      })
    ),
  }),
  refreshData: PropTypes.func,
};

const mapStateToProps = ({ filterConfig }) => ({
  filterConfig,
});

const mapDispatchToProps = dispatch => {
  return {
    refreshData: rows => dispatch(refreshData(rows)),
  };
};

const ColumnToggle = connect(mapStateToProps, mapDispatchToProps)(ConnectedColumnToggle);

export default ColumnToggle;
