/* eslint-disable no-shadow */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import CheckIcon from '@material-ui/icons/Check';
import ToggleButton from '@material-ui/lab/ToggleButton';
import { Tooltip } from '@material-ui/core';

import refreshData from '../actions';
import { DATAGRID_SORT_CONFIG } from '../constantas/settings';

const useStyles = makeStyles(theme => ({
  button: {
    width: 20,
    height: 25,
    '&:hover': {
      backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
      backgroundColor: theme.palette.secondary.light,
      '& svg': {
        fill: theme.palette.background.default,
      },
    },
  },
}));

const ConnectedSimpleToggleButton = ({ toggleStatus, refreshData, filterConfig }) => {
  const styles = useStyles();

  const config = JSON.parse(localStorage.getItem(DATAGRID_SORT_CONFIG));
  const isSelected = config.status.isShowOnlyOperating;

  const handleSelect = () => {
    toggleStatus(!isSelected);
    refreshData({
      filterConfig: {
        ...filterConfig,
        status: {
          isShowOnlyOperating: isSelected,
          statusToShow: isSelected ? 'operating' : 'all',
        },
      },
    });
  };
  return (
    <Tooltip
      title={isSelected ? 'Show all' : 'Show only operating'}
      aria-label="Show company status"
    >
      <ToggleButton
        value="check"
        selected={isSelected}
        className={styles.button}
        onChange={handleSelect}
      >
        <CheckIcon />
      </ToggleButton>
    </Tooltip>
  );
};

ConnectedSimpleToggleButton.defaultProps = {
  toggleStatus: () => {},
  refreshData: () => {},
  filterConfig: {},
};

ConnectedSimpleToggleButton.propTypes = {
  toggleStatus: PropTypes.func,
  refreshData: PropTypes.func,
  filterConfig: PropTypes.shape({
    orderBy: PropTypes.string,
    index: PropTypes.shape({
      sortDirection: PropTypes.string,
    }),
    company: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchValue: PropTypes.string,
    }),
    foundedYear: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchRange: PropTypes.arrayOf(PropTypes.number),
    }),
    founder: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchValue: PropTypes.string,
    }),
    fundingTotal: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchRange: PropTypes.arrayOf(PropTypes.number),
    }),
    businessTypes: PropTypes.shape({
      sortDirection: PropTypes.string,
      categories: PropTypes.arrayOf(PropTypes.bool),
    }),
    status: PropTypes.shape({
      isShowOnlyOperating: PropTypes.bool,
      statusToShow: PropTypes.string,
    }),
    searchByAllStr: PropTypes.string,
    columnConfig: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string,
        label: PropTypes.string,
        isVisible: PropTypes.bool,
      })
    ),
  }),
};

const mapStateToProps = ({ filterConfig }) => ({ filterConfig });

const mapDispatchToProps = dispatch => {
  return {
    refreshData: data => dispatch(refreshData(data)),
  };
};

const SimpleToggleButton = connect(
  mapStateToProps,
  mapDispatchToProps
)(ConnectedSimpleToggleButton);

export default SimpleToggleButton;
