/* eslint-disable no-shadow */
/* eslint-disable no-nested-ternary */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import { TableSortLabel, Checkbox } from '@material-ui/core';

import refreshData from '../../actions';
import { DATAGRID_SORT_CONFIG } from '../../constantas/settings';
import { HEAD_CELLS } from '../../constantas/data-generator-for';
import refreshLS from '../../utils/save-in-LS';
import toggleStatus from '../../utils/filters';
import ToggleButton from '../toggle-button';

const ConnectedTableHeader = props => {
  const {
    order,
    numSelected,
    rowCount,
    onRequestSort,
    refreshData,
    scrollLeft,
    filteredData,
    filterConfig,
  } = props;

  const useStyles = makeStyles(theme => ({
    head: {
      position: 'sticky',
      top: 0,
      zIndex: 2,
    },
    head2: {
      position: 'absolute',
      top: 0,
      display: 'flex',
      height: 60,
      zIndex: 2,
      '& div': {
        textAlign: 'center',
        lineHeight: '50px',
        borderRight: `1px solid ${theme.palette.secondary.light}`,
        borderTop: `1px solid ${theme.palette.secondary.light}`,
        '&:last-child': {
          borderRight: 'none',
        },
      },
      '& .MuiTableSortLabel-active': {
        '& .MuiTableSortLabel-icon': {
          fill: theme.palette.primary.main,
        },
      },
    },
    visuallyHidden: {
      border: 0,
      clip: 'rect(0 0 0 0)',
      height: 1,
      margin: -1,
      overflow: 'hidden',
      padding: 0,
      position: 'absolute',
      top: 20,
      width: 1,
    },
    checkbox: {
      minWidth: 50,
      width: 50,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
      padding: 0,
    },
    wrap: {
      border: 'none',
    },
    cell: {
      minWidth: 200,
      width: 200,
      height: 60,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
      zIndex: 2,
    },
    index: {
      minWidth: 45,
      height: 60,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
    },
    icon: {
      width: 55,
      minWidth: 50,
      height: 60,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
    },
    foundedYear: {
      width: 120,
      height: 60,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
    },
    fundingTotal: {
      width: 200,
      height: 60,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
    },
    status: {
      minWidth: 100,
      height: 60,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
      '& .Mui-selected': {
        backgroundColor: theme.palette.primary.light,
        '& svg': {
          fill: theme.palette.background.default,
        },
      },
    },
    company: {
      minWidth: 200,
      height: 60,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
      zIndex: 11,
    },
    stickyColumn: {
      position: 'absolute',
      top: 0,
      left: scrollLeft,
      width: 200,
      height: 60,
      backgroundColor: theme.palette.background.default,
      boxShadow: theme.shadows[1],
      zIndex: 11,
    },
    sortLabel: {
      '& svg': {
        opacity: 0.3,
        '&:hover': {
          opacity: 1,
        },
      },
    },
  }));
  const columnConfig = filterConfig.columnConfig ? filterConfig.columnConfig : HEAD_CELLS;

  const styles = useStyles();
  const config = JSON.parse(localStorage.getItem(DATAGRID_SORT_CONFIG));
  const { orderBy } = config;
  const renderCells =
    scrollLeft < 150
      ? [...columnConfig]
      : [...columnConfig, { id: 'companySticky', label: 'Company', isVisible: true }];

  const handleSort = property => () => {
    onRequestSort(property);
  };
  // TODO при изменении shownData количество выбранных должно изменить тоже

  const handleSelectAllClick = ({ target: { checked } }) => {
    if (checked) {
      const allSelected = filteredData.map(n => n.id);
      refreshData({ selected: allSelected });
    } else {
      refreshData({ selected: [] });
    }
  };
  const handleStatus = isSelected => {
    const newData = toggleStatus(isSelected, filteredData, refreshData, refreshLS, config);
    const newConfig = JSON.parse(localStorage.getItem(DATAGRID_SORT_CONFIG));
    refreshData({ filterConfig: newConfig });
    refreshData({ filteredData: newData });
  };

  return (
    <div className={styles.head}>
      <div className={styles.head2}>
        <div className={styles.checkbox}>
          <Checkbox
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === filteredData.length}
            onChange={handleSelectAllClick}
            inputProps={{ 'aria-label': 'select all' }}
          />
        </div>
        {renderCells.map(({ id, label, isVisible }) => {
          const divClass =
            id === 'icon'
              ? styles.icon
              : id === 'index'
              ? styles.index
              : id === 'foundedYear'
              ? styles.foundedYear
              : id === 'status'
              ? styles.status
              : id === 'fundingTotal'
              ? styles.fundingTotal
              : id === 'company'
              ? styles.company
              : id === 'companySticky'
              ? styles.stickyColumn
              : styles.cell;
          return (
            <div key={id} className={styles.wrap}>
              {isVisible ? (
                <div className={divClass}>
                  {['icon', 'geo'].includes(id) ? (
                    label
                  ) : id === 'status' ? (
                    <>
                      {`${label} `}
                      <ToggleButton toggleStatus={handleStatus} />
                    </>
                  ) : (
                    <TableSortLabel
                      active={orderBy === id}
                      direction={orderBy === id ? order : 'asc'}
                      onClick={handleSort(id)}
                      className={styles.sortLabel}
                    >
                      {label}
                      {orderBy === id ? (
                        <span className={styles.visuallyHidden}>
                          {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                        </span>
                      ) : null}
                    </TableSortLabel>
                  )}
                </div>
              ) : null}
            </div>
          );
        })}
      </div>
    </div>
  );
};

ConnectedTableHeader.defaultProps = {
  numSelected: 0,
  onRequestSort: () => {},
  order: 'asc',
  rowCount: 0,
  scrollLeft: 0,
  filteredData: [],
  refreshData: () => {},
  filterConfig: {},
};

ConnectedTableHeader.propTypes = {
  numSelected: PropTypes.number,
  onRequestSort: PropTypes.func,
  refreshData: PropTypes.func,
  order: PropTypes.oneOf(['asc', 'desc']),
  rowCount: PropTypes.number,
  scrollLeft: PropTypes.number,
  filteredData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      fundingTotal: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      businessTypes: PropTypes.arrayOf(PropTypes.string),
    })
  ),
  filterConfig: PropTypes.shape({
    orderBy: PropTypes.string,
    index: PropTypes.shape({
      sortDirection: PropTypes.string,
    }),
    company: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchValue: PropTypes.string,
    }),
    foundedYear: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchRange: PropTypes.arrayOf(PropTypes.number),
    }),
    founder: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchValue: PropTypes.string,
    }),
    fundingTotal: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchRange: PropTypes.arrayOf(PropTypes.number),
    }),
    businessTypes: PropTypes.shape({
      sortDirection: PropTypes.string,
      categories: PropTypes.arrayOf(PropTypes.bool),
    }),
    status: PropTypes.shape({
      isShowOnlyOperating: PropTypes.bool,
      statusToShow: PropTypes.string,
    }),
    searchByAllStr: PropTypes.string,
    columnConfig: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string,
        label: PropTypes.string,
        isVisible: PropTypes.bool,
      })
    ),
  }),
};

const mapStateToProps = ({ filteredData, filterConfig }) => ({ filteredData, filterConfig });

const mapDispatchToProps = dispatch => {
  return {
    refreshData: rows => dispatch(refreshData(rows)),
  };
};

const TableHeader = connect(mapStateToProps, mapDispatchToProps)(ConnectedTableHeader);

export default TableHeader;
