/* eslint-disable no-shadow */
/* eslint-disable react/no-array-index-key */
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import clsx from 'clsx';
import Checkbox from '@material-ui/core/Checkbox';

import theme from '../../themes/themeLight';
import refreshData from '../../actions';
import rowStyles from './row-styles';
import { HEAD_CELLS } from '../../constantas/data-generator-for';

const coordinateFormatting = n =>
  n.toLocaleString('ru-RU', { maximumFractionDigits: 2, minimumFractionDigits: 2 });

const ShowCoors = ({ coors, geoClass }) => {
  const lat = coordinateFormatting(coors.latitude);
  const long = coordinateFormatting(coors.longitude);
  return (
    <>
      <span>[</span>
      <span className={geoClass}>{lat}</span>
      <span>;&#8194;</span>
      <span className={geoClass}>{long}</span>
      <span>]</span>
    </>
  );
};

ShowCoors.defaultProps = {
  geoClass: '',
  coors: { latitude: 0, longitude: 0 },
};

ShowCoors.propTypes = {
  geoClass: PropTypes.string,
  coors: PropTypes.shape({ latitude: PropTypes.number, longitude: PropTypes.number }),
};

const ConnectedRowTemplate = props => {
  const { item, selected, refreshData, scrollLeft, filterConfig } = props;
  const styles = rowStyles(theme);
  const {
    index,
    id,
    icon,
    company,
    url,
    foundedYear,
    founder,
    fundingTotal,
    status,
    geo,
    businessTypes,
  } = item;
  const isSelected = name => selected.indexOf(name) !== -1;
  const [isItemSelected, setIsItemSelected] = useState(false);

  const renderCells = filterConfig.columnConfig ? filterConfig.columnConfig : HEAD_CELLS;

  useEffect(() => {
    setIsItemSelected(isSelected(id));
  }, [selected, id]);

  const labelId = `enhanced-table-checkbox-${index}`;

  const handleSelect = () => {
    const selectedItems = isItemSelected ? selected.filter(el => el !== id) : [...selected, id];

    setIsItemSelected(!isItemSelected);
    refreshData({ selected: selectedItems });
  };

  return (
    <div className={styles.row} key={id}>
      <div
        className={clsx(styles.center, styles.checkbox, {
          [styles.cellSelected]: isItemSelected,
        })}
      >
        <Checkbox
          checked={isItemSelected}
          inputProps={{ 'aria-labelledby': labelId }}
          onChange={handleSelect}
          color="secondary"
          className={isItemSelected ? styles.checkboxChecked : null}
        />
      </div>
      {filterConfig.columnConfig[0].isVisible ? (
        <div
          className={clsx(styles.index, styles.center, { [styles.cellSelected]: isItemSelected })}
        >
          {index + 1}
        </div>
      ) : null}
      {filterConfig.columnConfig[1].isVisible ? (
        <div
          className={clsx(styles.icon, styles.center, { [styles.cellSelected]: isItemSelected })}
        >
          <img src={icon} alt={company} className={styles.img} />
        </div>
      ) : null}
      {renderCells[2].isVisible ? (
        <div
          className={clsx(styles.company, styles.textVerCenter, {
            [styles.cellSelected]: isItemSelected,
            [styles.whiteBg]: !isItemSelected,
          })}
        >
          <a href={url} target="_blank" rel="noopener noreferrer" className={styles.link}>
            {company}
          </a>
        </div>
      ) : null}
      {renderCells[2].isVisible && scrollLeft > 150 ? (
        <div
          style={{ left: scrollLeft }}
          className={clsx(styles.stickyColumn, styles.textVerCenter, {
            [styles.cellSelected]: isItemSelected,
            [styles.whiteBg]: !isItemSelected,
          })}
        >
          <a href={url} target="_blank" rel="noopener noreferrer" className={styles.link}>
            {company}
          </a>
        </div>
      ) : null}
      {renderCells[3].isVisible ? (
        <div
          className={clsx(styles.foundedYear, styles.textVerCenter, {
            [styles.cellSelected]: isItemSelected,
          })}
        >
          {foundedYear}
        </div>
      ) : null}
      {renderCells[4].isVisible ? (
        <div
          className={clsx(styles.cell, styles.textVerCenter, {
            [styles.cellSelected]: isItemSelected,
          })}
        >
          {founder}
        </div>
      ) : null}
      {renderCells[5].isVisible ? (
        <div
          className={clsx(styles.fundingTotal, styles.textVerCenter, {
            [styles.cellSelected]: isItemSelected,
          })}
        >
          {fundingTotal.toLocaleString()}
        </div>
      ) : null}
      {renderCells[6].isVisible ? (
        <div
          className={clsx(styles.cell, styles.textVerCenter, {
            [styles.cellSelected]: isItemSelected,
          })}
        >
          {businessTypes.map((category, i) => (
            <span key={`${id}-${category}-${i}`}>
              |&nbsp;
              <span className={styles.highlightedText}>{category === '' ? null : category}</span>
              {i === businessTypes.length - 1 ? ' |' : null}
            </span>
          ))}
        </div>
      ) : null}
      {renderCells[7].isVisible ? (
        <div
          className={clsx(styles.status, styles.textVerCenter, {
            [styles.cellSelected]: isItemSelected,
          })}
        >
          {status}
        </div>
      ) : null}
      {renderCells[8].isVisible ? (
        <div
          className={clsx(styles.cell, styles.textVerCenter, {
            [styles.cellSelected]: isItemSelected,
          })}
        >
          <ShowCoors coors={geo} geoClass={styles.highlightedText} />
        </div>
      ) : null}
    </div>
  );
};

ConnectedRowTemplate.defaultProps = {
  item: {},
  selected: [],
  refreshData: () => {},
  scrollLeft: 0,
  filterConfig: {},
};

ConnectedRowTemplate.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.string,
    index: PropTypes.number,
    icon: PropTypes.string,
    company: PropTypes.string,
    url: PropTypes.string,
    foundedYear: PropTypes.number,
    founder: PropTypes.string,
    fundingTotal: PropTypes.number,
    status: PropTypes.string,
    geo: PropTypes.shape({
      latitude: PropTypes.number,
      longitude: PropTypes.number,
    }),
    businessTypes: PropTypes.arrayOf(PropTypes.string),
  }),
  selected: PropTypes.arrayOf(PropTypes.string),
  refreshData: PropTypes.func,
  scrollLeft: PropTypes.number,
  filterConfig: PropTypes.shape({
    orderBy: PropTypes.string,
    index: PropTypes.shape({
      sortDirection: PropTypes.string,
    }),
    company: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchValue: PropTypes.string,
    }),
    foundedYear: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchRange: PropTypes.arrayOf(PropTypes.number),
    }),
    founder: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchValue: PropTypes.string,
    }),
    fundingTotal: PropTypes.shape({
      sortDirection: PropTypes.string,
      searchRange: PropTypes.arrayOf(PropTypes.number),
    }),
    businessTypes: PropTypes.shape({
      sortDirection: PropTypes.string,
      categories: PropTypes.arrayOf(PropTypes.bool),
    }),
    status: PropTypes.shape({
      isShowOnlyOperating: PropTypes.bool,
      statusToShow: PropTypes.string,
    }),
    searchByAllStr: PropTypes.string,
    columnConfig: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string,
        label: PropTypes.string,
        isVisible: PropTypes.bool,
      })
    ),
  }),
};

const mapStateToProps = ({ selected, filterConfig }) => ({ selected, filterConfig });

const mapDispatchToProps = dispatch => {
  return {
    refreshData: data => dispatch(refreshData(data)),
  };
};
const RowTemplate = connect(mapStateToProps, mapDispatchToProps)(ConnectedRowTemplate);
export default RowTemplate;
