import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import CsvDownloader from 'react-csv-downloader';
import clsx from 'clsx';
import { lighten, makeStyles } from '@material-ui/core/styles';
import { Toolbar, Typography, IconButton, Tooltip } from '@material-ui/core';
import GetAppIcon from '@material-ui/icons/GetApp';
import DeleteIcon from '@material-ui/icons/Delete';

import dataToCSVPreparation from '../../utils/data-to-CSV-preparation';
import ColumnToggle from '../column-toggle';

const useToolbarStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'flex-end',
    height: 100,
  },
  highlight:
    theme.palette.type === 'light'
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
  description: {
    color: theme.palette.primary.main,
    maxWidth: 400,
    '& *': {
      paddingBottom: 10,
      display: 'block',
    },
  },
  download: {
    padding: '5px 5px 0',
  },
}));

const ConnectedTableToolbar = props => {
  const classes = useToolbarStyles();
  const { selected, filteredData, handleDelete } = props;
  const numSelected = selected.length;
  const [csvData, setCsvData] = useState(dataToCSVPreparation(filteredData));

  useEffect(() => {
    setCsvData(dataToCSVPreparation(filteredData));
  }, [filteredData, selected]);

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      {numSelected > 0 ? (
        <Typography color="primary" variant="subtitle1" className={classes.description}>
          {numSelected} selected
        </Typography>
      ) : (
        <div className={classes.description}>
          <Typography variant="h3" id="tableTitle">
            StartUp Investments
          </Typography>
          <Typography variant="body2">
            Information about startup companies and investment
          </Typography>
        </div>
      )}
      {numSelected > 0 ? (
        <Tooltip title="Delete">
          <IconButton aria-label="delete" onClick={handleDelete}>
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      ) : (
        <div className={classes.tools}>
          <Tooltip title="download CSV">
            <IconButton aria-label="download CSV" className={classes.download}>
              <CsvDownloader datas={csvData} filename="csvData.csv" separator=";">
                <GetAppIcon />
              </CsvDownloader>
            </IconButton>
          </Tooltip>
          <ColumnToggle />
        </div>
      )}
    </Toolbar>
  );
};
ConnectedTableToolbar.defaultProps = {
  selected: [],
  filteredData: [],
  handleDelete: () => {},
};

ConnectedTableToolbar.propTypes = {
  selected: PropTypes.arrayOf(PropTypes.string),
  handleDelete: PropTypes.func,
  filteredData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      investSum: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      businessTypes: PropTypes.arrayOf(PropTypes.string),
    })
  ),
};

const mapStateToProps = ({ selected, filteredData }) => ({
  selected,
  filteredData,
});

const TableToolbar = connect(mapStateToProps, null)(ConnectedTableToolbar);

export default TableToolbar;
